import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import 'jquery-csv'
import * as d3 from 'd3';
import * as dat from 'lil-gui'



// Scene
const scene = new THREE.Scene()

const gui = new dat.GUI()

let geometry=null
let material=null
let mesh = null

d3.csv('suf1.csv').then(data => {
    // Parse the csv data and store it in a variable
    const verticesData = data;
  
    // Create a buffer geometry
     geometry = new THREE.BufferGeometry();
    
    // Extract the vertex coordinates from the csv data and store them in a typed array
    const params = {FunctionalTrait: 'kr', Creation: true}
    const options = {suf:'suf', jxl:'jxl',jr:'jr', kr:'kr', kx:'kx', psi:'psi'}
    

    const GenerateArch = () => {

    if (mesh !== null){
        geometry.dispose()
        material.dispose()
        scene.remove(mesh)
    }

    let NumVertices = Math.round(verticesData.length /2)
    const vertices = new Float32Array(NumVertices * 3);
    const colors = new Float32Array(NumVertices * 3)
    const vertsizes = new Float32Array(NumVertices)


    
    let zSum = 0;
    for (let i = 0; i < NumVertices; i++) {
        zSum += parseFloat(verticesData[i].z1);
    }
    const zMean = zSum / NumVertices;

    
    //const dropdown = gui.add(params, 'tovisu', options);

    let max = 0
    for (let i = 0; i < NumVertices; i++) {
        if (verticesData[i][params.FunctionalTrait]>max){
            max = verticesData[i][params.FunctionalTrait]
        }
    }

    const startColor = new THREE.Color(0x00f0FF);
    const endColor = new THREE.Color(0xFF00f0); 
    

    for (let i = 0; i < NumVertices; i++) {
      vertices[i * 3] = verticesData[i].x1
      vertices[i * 3 + 1] = (verticesData[i].z1 - zMean)/6
      vertices[i * 3 + 2] = verticesData[i].y1
        
      let color = new THREE.Color()
      color.lerpColors(startColor, endColor, verticesData[i][params.FunctionalTrait]/max)
      colors[i * 3] = color.r
      colors[i * 3 + 1] = color.g
      colors[i * 3 + 2] = color.b

      vertsizes[i] = verticesData[i].radius
    }


    // Set the attributes of the buffer geometry
    geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3));
    geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3));
    geometry.setAttribute('size', new THREE.BufferAttribute(vertsizes, 1));


    
    // Create a mesh using the buffer geometry and a material
     material = new THREE.PointsMaterial();
    material.size = 0.16

    material.sizeAttenuation = true
    //material.color = new THREE.Color('#ff88cc')

    const textureloader = new THREE.TextureLoader()
    const particletext = textureloader.load("1.png")   
    material.map = particletext
    material.transparent=true
    material.alphaMap=particletext
    //material.alphaTest=0.001
    //material.depthTest=false
    material.depthWrite=false
    //material.blending=THREE.AdditiveBlending
    material.vertexColors = true

     mesh = new THREE.Points(geometry, material);
  
    // Add the mesh to the scene
    scene.add(mesh);

    // Sizes
let sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

// Camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height)
camera.position.z = 15
scene.add(camera)


// mouse camera

const cursor = {x:0,y:0}

window.addEventListener("mousemove", (event)=> {

    cursor.x = event.clientX/sizes.width-0.5
    cursor.y = (-event.clientY/sizes.height)+0.5

})

// Handle resize

window.addEventListener("resize", ()=>{
    sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    }
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio,2))
})


const clock = new THREE.Clock()

let currentVertex = 0;
const tick =() => {
    const elapsedTime = clock.getElapsedTime()
    mesh.rotation.y = elapsedTime*0.02

    
  
    mesh.geometry.setDrawRange(0, currentVertex + 1);
    currentVertex+=100;
    
    controls.update()
    renderer.render(scene, camera)

    window.requestAnimationFrame(tick)
    
}

const canvas = document.querySelector('canvas.webgl')

// Controls

const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio,2))
renderer.render(scene, camera)
if (params.Creation === true) {tick()}

    }

gui.add(params, 'FunctionalTrait', options).onFinishChange(GenerateArch)
gui.add(params, 'Creation').onFinishChange(GenerateArch)

GenerateArch()

})



